.navbar {
    display: flex;
    justify-content: center;
    border: 1px solid green;
    background: transparent;
}

.navbar>ul {
    display: flex;
    text-decoration: none;
    list-style-type: none;
}

.navbar>ul>li {
    text-decoration: none;
    display: inline;
    padding-left: 30px;
    color: white;
}