.horoscope {
    background: linear-gradient(#e66465, #9198e5);
}

.grid {
    display: grid;
    grid-template-columns: 2fr 2fr 1.4fr;
    grid-gap: 40px;
}

.grid>button {
    border: none;
    color: rgb(42, 119, 219);
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-flex;
    font-size: 24px;
    margin: 4px 2px;
    cursor: pointer;
}